</template>
<script>
export default {
  props: {
    title:{
        default: "软件定做"
    },
    icon:{
        default:"mdi-menu"
    }
  },
  data() {
    return {
      selecedTabTitle: ""
    };
  },
  mounted() {
    //this.selecedTabTitle=this.vdata.language[0];
    
  },
  methods: {
    tabClick(item) {
